	import {mapState} from 'vuex';
	
    export default {
        name: "app-sup-vip",
	    props: {
            discount: {
                type: String,
	            default() {
                    return null;
                }
            },
            is_vip_card_user: {
                type: Number,
                default() {
                    return 0;
                }
            },
            margin: {
                type: String,
	            default() {
                    return '0';
	            }
            }
	    },
        data() {
            return {
                is_show: false,
            }
        },
        created() {
            let that = this;
            if(that.is_vip_card_user == 1) {
                if(that.mall.setting.is_show_super_vip == '1'){
                    that.is_show = true;
                }else {
                    that.is_show = false;
                }
            }else {
                if(that.mall.setting.is_show_normal_vip == '1'){
                    that.is_show = true;
                }else {
                    that.is_show = false;
                }
            }
        },
	    computed: {
            ...mapState({
                mall: state => state.mallConfig.mall,
                userInfo: state => state.user.info,
            })
	    }
    }
